// light button for desktop
%light-btn-desk {
  padding: 27px 23px 26px 24px;
  color: $orange;
  border: 2px solid $orange;
  transition: all $transSet;
  &:hover,
  &:focus,
  &:active {
    color: $white-font;
    background-color: $orange;
    cursor: pointer;
  }
}

// orage button for desktop
%orange-btn-desk {
  padding: 27px 23px 26px 24px;
  color: $white-font;
  border: 2px solid $orange;
  background-color: $orange;
  transition: all $transSet;
  &:hover,
  &:focus,
  &:active {
    color: $orange;
    background-color: #ffffff;
    cursor: pointer;
  }
}

